.App {
  text-align: center;
}

.App-logo {
  margin-top: 180px;
  height: 50vmin;
  pointer-events: none;
  border-radius: 20px;
}

/*
@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}*/

.App-header {
  background-image: linear-gradient(to bottom right, #a86c3c, #5e1c0f);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.letter-link {
  color: #fcfcfc;
  margin: 20px;
}

.letter-link:hover {
  color: #afc226;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.heart {
  width: 50px;
  height: 50px;
  animation: float 1.2s ease-in;
}

@keyframes float {
  0% { transform: translateY(0); opacity: 1; }
  100% { transform: translateY(-50px); opacity: 0; }
}


.App-body{
  border-radius: 20px;
  padding: 2px;
  background-color: #824325;
  margin-top: 100px;
  max-width: 900px;
  margin-bottom: 20px;
}


.important_text{
  font-size: 3ch;
}